                class="transition-class"
                appear
            >
                <div
                    :class="{
                        'menu__item--fullwidth': index == goTo,
                        'menu__item--nowidth': (goTo != null) & (goTo != index)
                    }"
                    class="menu__item"
                    v-for="(enlace, index) in enlaces"
                    @click="set_goTo(index, enlace.name)"
                    :key="enlace.label"
                    :style="{ 'background-color': enlace.background }"
