<script>
export default {
    name: "index",
    data() {
        return {
            files_list: [
                "inicio_circuit1t.svg",
                "inicio_circuit2t.svg",
                "inicio_circuit3t.svg",
                "inicio_circuit4t.svg"
            ],
            enlaces: [],
            enlaces_holder: [
                {
                    label: "Quienes somos",
                    name: "bienvenida",
                    color: "#fbc509ff",
                    background: "#397ec0ff"
                },
                {
                    label: "Planeacin institucional",
                    name: "gestion_p_index",
                    color: "#8a197fff",
                    background: "#eb6117ff"
                },
                {
                    label: "Justicia en tiempos de COVID-19",
                    name: "presentacion",
                    color: "#ffffffff",
                    background: "#262b61ff"
                },
                {
                    label: "Gestin judicial y consultiva",
                    name: "gestion",
                    color: "#308f82ff",
                    background: "#fac207ff"
                },
                {
                    label: "Comunicacin",
                    name: "comunicacion",
                    color: "#282b63ff",
                    background: "#078f82ff"
                },
                {
                    label: "Desafos",
                    name: "desafios",
                    color: "#110f0cff",
                    background: "#8a197fff"
                },
                {
                    label: "Consulta ciudadana",
                    name: "consulta",
                    color: "#fac205ff",
                    background: "#e3192eff"
                }
            ],
            current_file: 0,
            index_enlaces: 0,
            show_logo: false,
            goTo: null,
            is_mobile: false
        }
    },
    methods: {
        set_goTo(index, name) {
            this.goTo = index
            setTimeout(() => {
                this.$router.push({ name: name })
                console.log(name)
            }, 1000)
        },

        change_file() {
            if (this.current_file + 1 > this.files_list.length) {
                this.current_file = 0
            } else {
                this.current_file = this.current_file + 1
            }

            if (this.index_enlaces == 0) {
                setTimeout(() => {
                    this.change_file()
                }, 500)
            }

            return
        },

        show_menu() {
            const enlace_obj = this.enlaces_holder[this.index_enlaces]
            this.enlaces.push(enlace_obj)
            this.index_enlaces = this.index_enlaces + 1
            if (this.index_enlaces + 1 <= this.enlaces_holder.length) {
                setTimeout(() => {
                    this.show_menu()
                }, 200)
            }
        },

        handleResize() {
            const isMobile = window.matchMedia(
                "(max-width: 720px), (max-height: 410px)"
            )
            this.is_mobile = isMobile.matches ? true : false
        }
    },
    mounted() {
        window.addEventListener("resize", this.handleResize)
        this.change_file()
        setTimeout(() => {
            this.show_logo = true
        }, 1500)
        this.handleResize()
    },
    beforeDestroy() {
        window.removeEventListener("resize", this.handleResize)
    }
}
</script>
